import React, { ReactNode, useRef } from 'react';
import { AriaDialogProps, mergeProps, useFocusRing } from 'react-aria';
import { useDialog } from 'react-aria';
import classNames from 'classnames';

import styles from './Dialog.module.scss';

export interface DialogProps extends AriaDialogProps {
    className?: string;
    children: ReactNode;
}

const Dialog = ({ children, className, ...props }: DialogProps) => {
    const ref = useRef(null);
    const { dialogProps } = useDialog(props, ref);
    const { isFocusVisible, focusProps } = useFocusRing();

    const classList = classNames(styles.dialog, className, {
        [styles['focus-ring']]: isFocusVisible,
    });

    return (
        <div
            {...mergeProps(dialogProps, focusProps)}
            className={classList}
            ref={ref}
        >
            {children}
        </div>
    );
};

export default Dialog;
